.game-screen-fullscreen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #000;
  z-index: 9999;
}

.emulator-fullscreen {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Ensure emulator content fills the screen */
.emulator-fullscreen > * {
  max-width: 100vw;
  max-height: 100vh;
}

/* Hide any default emulator UI borders/padding */
.emulator-fullscreen canvas {
  width: 100vw !important;
  height: 100vh !important;
  object-fit: contain;
}